//TODO: change to HashRouter if BrowserRouter doesn't work
import { BrowserRouter as Router, Switch, Route } from 'react-router-dom';
import { Navbar, Nav } from 'react-bootstrap';
import { Home, About, Projects, Contact } from './pages.js';
import './App.scss';


// <Navs links= {{
//   "/": "Welcome",
// }} />
function Navs(props) {
  const selected = {};
  const location = window.location.href.match('//[^/]*([/].*)')[1];
  // console.log(location);
  for (const key of Object.keys(props.links)) {
    if (key === location) {
      selected[key] = true;
    } else {
      selected[key] = false;
    }
  }
  const children = Object.entries(props.links).map(([loc, title]) => {
    return <Nav.Link key={loc} href={loc} active={selected[loc]}>{title}</Nav.Link>
  });
  return (
    <Nav className="mr-auto">
      {children}
    </Nav>
  );
}

function App() {
  return (
    <div id="router">
      <Router>
        <div id="mainPhoto">
          <div id ="mainContent">
            <h1 id="mainTitle">Derrick Lockwood</h1>
            <h2 id="subTitle">Software Developer</h2>
          </div>
        </div>
        <Navbar className="sticky-nav" bg="primary" variant="dark" expand="lg">
          <Navbar.Brand href="/">
            <img
            src="./profile.ico"
            width="30"
            height="30"
            className="d-inline-block align-top"
            alt="Derrick Lockwood Logo"
            />
          </Navbar.Brand>
          <Navbar.Toggle aria-controls="basic-navbar-nav" />
          <Navbar.Collapse id="basic-navbar-nav">
            <Navs links={{
              "/": "Welcome",
              "/about": "About",
              "/projects": "Projects",
              "/contact": "Contact",
            }}/>
          </Navbar.Collapse>
        </Navbar>
        <Switch>
          <Route exact path="/">
            <Home/>
          </Route>
          <Route exact path="/about">
            <About/>
          </Route>
          <Route exact path="/projects">
            <Projects/>
          </Route>
          <Route exact path="/contact">
            <Contact/>
          </Route>
        </Switch>
      </Router>
    </div>
  );
}

export default App;
