function Section(props) {
  let child = null;
  if (props.childIsRow) {
    child = (
      <Col md={{span: 9, offset: 1}}>
        <Row className="m-3">
          {props.children}
        </Row>
      </Col>
    );
  } else {
    child = (
      <Row className="m-3">
        <Col md={{span: 9, offset: 1}}>
          {props.children}
        </Col>
      </Row>
    );
  }

  return (
    <div>
      <Row className="m-3">
        <Col md={{span: 4, offset: 1}}>
          <h4>{props.name}</h4>
        </Col>
      </Row>
      {child}
    </div>
  );
}


class Skill extends React.Component {

  constructor(props) {
    super(props);
    this.state = {
      inProp: false
    }
    this.barRef = React.createRef();
  }

  componentDidMount() {
    window.addEventListener('scroll', this.handleScroll.bind(this));
  }

  componentWillUnmount() {
    window.removeEventListener('scroll', this.handleScroll.bind(this));
  }

  handleScroll(event) {
    if (this.barRef.current) {
      const skillTop = this.barRef.current.getBoundingClientRect().top;
      if (skillTop >= 0 && skillTop <= window.innerHeight) {
        this.setState({...this.state, inProp:true});
      }
    }
  }

  //TODO: Make progress bar grow with window scroll? window.innerHeight / 33? instead of Transition
  render() {
    return (
      <div class="mb-3" ref={this.barRef}>
        <h5>{this.props.name}</h5>
        <Transition in={this.state.inProp} timeout={1000}>
          {state => {
            if (state === "entered") {
              return (
                <ProgressBar>
                  <ProgressBar label={"Basic"} variant="warning" now={33} key={1} />
                  {this.props.lvl > 1 && <ProgressBar label={"Proficient"} variant="success" now={33} key={2} />}
                  {this.props.lvl > 2 && <ProgressBar label={"Extensive"} now={34} key={3} />}
                </ProgressBar>
              );
            } else {
              return (
                <ProgressBar>
                  <ProgressBar label={"Basic"} variant="warning" now={0} key={1} />
                  {this.props.lvl > 1 && <ProgressBar label={"Proficient"} variant="success" now={0} key={2} />}
                  {this.props.lvl > 2 && <ProgressBar label={"Extensive"} now={0} key={3} />}
                </ProgressBar>
              );
            }
          }}
        </Transition>
      </div>
    );
  }
}

function Qoute(props) {
  return (
    <Row className="text-center font-italic pb-5" md={{offset: 1, span: 2}}>
      <Col>
        <p>"{props.qoute}" - {props.name}</p>
      </Col>
    </Row>
  );
}

function getSkills(skillObj) {
  let skills = [];
  for (const [key, value] of Object.entries(skillObj)) {
    skills.push(<Skill name={key} lvl={value}/>);
  }
  return skills;
}

